import React from "react";
import { ITodo } from "../../intefaces";

import "./TodoList.css";

type TodoListProps = {
  todos: ITodo[];
  onToggle: (id: number) => void;
  onRemove: (id: number) => void;
};

const TodoList: React.FC<TodoListProps> = ({ todos, onRemove, onToggle }) => {
  if (todos.length === 0) {
    return <p className="center">There is no tasks</p>;
  }
  const deleteOnClick = (id: number) => (e: React.MouseEvent) => {
    e.preventDefault();
    onRemove(id);
  };
  const toggleOnClick = (id: number) => () => {
    onToggle(id);
  };
  return (
    <ul>
      {todos.map(todo => {
        const classes = ["todo"];
        if (todo.completed) {
          classes.push("completed");
        }

        return (
          <li className={classes.join(" ")} key={todo.id}>
            <label>
              <input
                type="checkbox"
                checked={todo.completed}
                onChange={toggleOnClick(todo.id)}
              />
              <span>{todo.title}</span>
              <i
                className="material-icons red-text"
                onClick={deleteOnClick(todo.id)}
              >
                delete
              </i>
            </label>
          </li>
        );
      })}
    </ul>
  );
};

export default TodoList;
